.search-container {
    width: auto;
    display: inline-block;
    .more {
        padding-right: 20px;
        padding-left: 8px;
        position: relative;
        i {
            font-size: 20px !important;
            position: absolute;
            right: 2px;
            top: 7px;
        }
    }
    .advanced-search-wrapper {
        width: 100%;
        height: auto;
        background: #fff;
        position: absolute;
        top: 52px;
        left: 0;
        z-index: 999;
        -webkit-box-shadow: 0 3px 6px 1px #dcdee5;
        box-shadow: 0 3px 6px 1px #dcdee5;
        box-sizing: border-box;
        padding: 10px;
    }
    /deep/.search-form {
        width: 100%;
        padding: 10px;
        box-sizing: border-box;
        .bk-form-item {
            display: inline-flex;
            width: 23.5%;
            margin-bottom: 25px;
            margin-left: 16px;
            .bk-label {
                width: 70px !important;
                padding: 0 8px 0 0;
                span {
                    display: inline-block;
                    width: 100%;
                    line-height: 32px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
            }
            .bk-form-content {
                width: 100%;
                flex: 1;
            }
        }
        .bk-form-item:nth-child(4n+1) {
            margin-left: 0 !important;
        }
        .button-wrapper {
            width: 100%;
            display: block;
            text-align: center;
            height: auto;
            overflow: hidden;
            margin: 20px 5px 0;
            button {
                margin-right: 8px;
            }
        }
    }
}
