.table-container {
    background: #fff;
    border: 1px solid #dfe0e5;
}
/deep/ .bk-table::before {
    height: 0;
}
/deep/ .setting-title {
    text-align: right;
    > label {
        float: left !important;
    }
}
/deep/.bk-table-scrollable-x {
    .bk-table-body-wrapper {
        &::-webkit-scrollbar {
            /*滚动条整体样式*/
            width: 10px;  /*高宽分别对应横竖滚动条的尺寸*/
            height: 8px;
            border: none;
        }
        &::-webkit-scrollbar-thumb {
            /*滚动条里面小方块*/
            border-radius: 10px;
            box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
            background: #D2D2D2;
        }
        &::-webkit-scrollbar-track {
            /*滚动条里面轨道*/
            border-radius: 15px;
            background: #ededed;
        }
    }
}
.bk-table-setting-content {
    /deep/ .content-title {
        padding-bottom: 5px;
        margin-bottom: 10px;
        border-bottom: 1px solid #eee;
    }
    /deep/ .content-line-height {
        display: none;
    }
    /deep/ .content-options {
        button {
            border-radius: 2px;
            min-width: 60px;
            letter-spacing: 2px;
        }
    }
}
/deep/ .bk-table-fixed {
    th {
        border: none !important;
    }
}
/deep/ .bk-table::before {
    height: 0;
}
/deep/ .bk-table-body {
    tr:last-child {
        td {
            //border-bottom: 0px;
        }
    }
}
/deep/ .bk-table-column-setting {
    border-left: 0;
}
